	module.exports = {"data":{"markdownRemark":{"html":"<p>One of the first projects I did with React was “Captain McGiblets Book of Nautical Secrets“, a CRM and project tracking tool. Built on <a class=\"effect-3\" href=\"https://www.meteor.com/\" target=\"_blank\">MeteorJS</a> and <a class=\"effect-3\" href=\"https://facebook.github.io/react/\" target=\"_blank\">React</a>, this web app is blazing fast and fun to use!</p>\n<html><head></head><body>\n  <a class=\"gatsby-resp-image-link\" href=\"/static/captain-m-graphic-f37b6907f327cc031293f6dae19fda48-0c2ba.jpg\" style=\"display: block\" target=\"_blank\" rel=\"noopener\">\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; \">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 57.8125%;position: relative; width: 100%; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"App Graphic\" title=\"\" src=\"/static/captain-m-graphic-f37b6907f327cc031293f6dae19fda48-40580.jpg\" srcset=\"/static/captain-m-graphic-f37b6907f327cc031293f6dae19fda48-c24b1.jpg 148w,\n/static/captain-m-graphic-f37b6907f327cc031293f6dae19fda48-7d6c5.jpg 295w,\n/static/captain-m-graphic-f37b6907f327cc031293f6dae19fda48-40580.jpg 590w,\n/static/captain-m-graphic-f37b6907f327cc031293f6dae19fda48-fcb13.jpg 885w,\n/static/captain-m-graphic-f37b6907f327cc031293f6dae19fda48-cfcae.jpg 1180w,\n/static/captain-m-graphic-f37b6907f327cc031293f6dae19fda48-0c2ba.jpg 1280w\" sizes=\"(max-width: 590px) 100vw, 590px\">\n    </span>\n  </span>\n  \n  </a>\n    </body></html>\n<p>User roles were a big priority for the end product. Different levels of users can view different entities entered into the CRM and monitor updates associated with their projects, posted by lower-level users. These updates can even include different attachments of any type!</p>\n<html><head></head><body>\n  <a class=\"gatsby-resp-image-link\" href=\"/static/captain-m-dash-1-1362d423e9b8247221ebeca6c98c8941-0c2ba.jpg\" style=\"display: block\" target=\"_blank\" rel=\"noopener\">\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; \">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 57.8125%;position: relative; width: 100%; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\" alt=\"App Dashboard\" title=\"\" src=\"/static/captain-m-dash-1-1362d423e9b8247221ebeca6c98c8941-40580.jpg\" srcset=\"/static/captain-m-dash-1-1362d423e9b8247221ebeca6c98c8941-c24b1.jpg 148w,\n/static/captain-m-dash-1-1362d423e9b8247221ebeca6c98c8941-7d6c5.jpg 295w,\n/static/captain-m-dash-1-1362d423e9b8247221ebeca6c98c8941-40580.jpg 590w,\n/static/captain-m-dash-1-1362d423e9b8247221ebeca6c98c8941-fcb13.jpg 885w,\n/static/captain-m-dash-1-1362d423e9b8247221ebeca6c98c8941-cfcae.jpg 1180w,\n/static/captain-m-dash-1-1362d423e9b8247221ebeca6c98c8941-0c2ba.jpg 1280w\" sizes=\"(max-width: 590px) 100vw, 590px\">\n    </span>\n  </span>\n  \n  </a>\n    </body></html>","frontmatter":{"path":"/captain-mcgiblets-book-nautical-secrets","title":"Captain McGiblet's Book of Nautical Secrets - Meteor App","image":null}}},"pathContext":{}}

